import React, { useState, useContext, useEffect } from 'react';
import Navbar from '../../parts/Navbar';
import Footer from '../../parts/Footer';

import { IconContext } from 'react-icons';
import * as BsIcons from 'react-icons/bs';

import styles from './Swap.module.css';
import { WalletContext } from '../../context/WalletContextProvider';

const Swap = () => {
  const [dataWallet] = useContext(WalletContext);
  const [finalValue, setFinalValue] = useState(0);
  const [inputValue, setInputValue] = useState(0.05);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const calculation = (e) => {
    setInputValue(e.target.value);
    let value = e.target.value;
    let total = value * 1600;
    setFinalValue(total);
  };

  const Buy = () => {
    if (inputValue < 0.05) {
      alert('Minimum 0.05');
    } else if (inputValue >= 0.05 && inputValue <= 1) {
      alert('Coming Soon');
    } else if (inputValue > 1) {
      alert(`Can't buy more than 1 ETH`);
    }
  };
  return (
    <div id="swap_presale">
      <Navbar />
      <main className="container mx-auto my-28">
        <div className="swap_container w-full grid place-items-center">
          <div className="content_wrap w-5/12 lg:w-54prc md:w-full">
            <div
              className={`${styles} swap_wrap w-full border-2 border-gray-800 p-6 rounded-xl`}
            >
              <div className={`${styles.input_eth} relative`}>
                <input
                  value={inputValue}
                  type="number"
                  className="font-medium text-2xl w-full p-4 pr-12 rounded-lg outline-none bg-abu-primary text-white"
                  onChange={calculation}
                />
              </div>
              <IconContext.Provider value={{ color: '#ffff' }}>
                <span className="grid place-items-center py-4">
                  <BsIcons.BsArrowDownUp />
                </span>
              </IconContext.Provider>
              <div className={`${styles.input_wks} relative`}>
                <input
                  type="number"
                  className="font-medium text-2xl w-full p-4 pr-12 rounded-lg outline-none bg-abu-primary text-white"
                  value={finalValue.toFixed(0)}
                  readOnly
                />
              </div>
              <h2 className="text-white font-bold text-sm py-4 text-right tracking-wide">
                {/* 1.000 $ETH = 1600 $WKS */}
                Coming Soon
              </h2>
              {dataWallet.account ? (
                <button
                  className="w-full font-bold text-sm uppercase text-center py-5 bg-white rounded-lg"
                  onClick={Buy}
                >
                  BUY NOW
                </button>
              ) : (
                <button
                  className="w-full font-bold text-sm uppercase text-center py-5 bg-white opacity-50 cursor-not-allowed rounded-lg"
                  disabled
                >
                  Connect wallet first
                </button>
              )}
            </div>
            {/* <div className="rules_wrap w-full mt-4 border-2 border-gray-700 rounded-xl py-6 px-8">
              <div className="w-full flex justify-between text-sm">
                <span className="text-abu-primary">Minimum received</span>
                <h3 className="text-white font-bold tracking-wide">0.05 ETH</h3>
              </div>
              <div className="w-full flex justify-between text-sm mt-2">
                <span className="text-abu-primary">Price impact</span>
                <h3 className="text-green-600 font-bold tracking-wide">0.1%</h3>
              </div>
              <div className="w-full flex justify-between text-sm mt-2">
                <span className="text-abu-primary">Liquidity Provider Fee</span>
                <h3 className="text-white font-bold tracking-wide">
                  0.0025 WKS
                </h3>
              </div> 
            </div> */}
          </div>
        </div>
      </main>
      <Footer />
    </div>
  );
};

export default Swap;
